    background: #fdfdfd; }
  50% { }
  100% {
    background: #fcfcfc; } }

@keyframes flashing {
  0% {
    background: #fdfdfd; }
  50% { }
  100% {
    background: #fcfcfc; } }

body,
#root {
  margin: 0;
  font-family: "Playfair Display", serif;
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #444; }

#messages {
  position: fixed;
  display: none;
  width: 50vw;
  padding: 0.5em;
  opacity: 0.94;
  margin: 0 auto;
  border-radius: 5px;
  top: 75px;
  left: 0;
  right: 0;
  text-align: center;
  cursor: pointer;
  color: #fff;
  z-index: 999; }
  #messages a {
    color: #444; }
  #messages a:hover {
    color: #aaa; }
  @media only screen and (max-width: 900px) {
    #messages {
      margin-top: 1em auto;
      width: calc(100vw - 5em); } }

a {
  text-decoration: none;
  outline: none;
  color: #43a047; }

a:hover {
  color: #5bbb60; }

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  box-shadow: 0 0 0 999px #fff inset !important;
  -webkit-text-fill-color: #444 !important; }

button {
  height: 55px; }

.btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-family: "Dosis", sans-serif;
  font-size: 16px;
  font-weight: normal;
  text-decoration: none;
  letter-spacing: 2px;
  color: #fff;
  background: #43a047;
  padding: 0.75em 1em;
  text-transform: uppercase;
  align-self: center;
  justify-self: center;
  cursor: pointer;
  border: none;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.17); }
  @media only screen and (max-width: 900px) {
    .btn {
      justify-self: center; } }

.btn:hover {
  color: #fff;
  text-decoration: none;
  background: #5bbb60; }

.grid {
  display: grid;
  margin: 0 auto;
  width: 70%;
  grid-template-columns: 1fr; }
  @media only screen and (max-width: 900px) {
    .grid {
      width: calc(100% - 2em);
      margin: 0 1em; } }

form,
.form {
  width: 100%; }

* {
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.17); }

